<template>
  <div>
  <h3>AddTodos</h3>
  <div class="add">
    <form @submit="onSubmit">
      <input type="text" v-model="title" placeholder="AddTodos....">
      <input type="submit" value="Submit">
    </form>
  </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex';
  export default {
    name:'AddTodos',
    data() {
      return {
       title:''
      }
    },
     methods: {
    ...mapActions([
      'addTodo', //also supports payload `this.nameOfAction(amount)` 
    ]),
    onSubmit (e){
     e.preventDefault();
     this.addTodo(this.title);
     this.title = '';
    }
  },
  }
</script>

<style  scoped>
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 10px;
  border: 1px solid #41b883;
  outline: 0;
}
input[type="submit"] {
  flex: 2;
  background: #41b883;
  color: #fff;
  border: 1px #41b883 solid;
  cursor: pointer;
}
</style>